@import './utils.scss';

.view-events {
    background-color: #D8EDFF;
    // display: flex;
    // flex-direction: column;
}

.view-events-container {
    display: flex;
    flex-direction: column;
    // justify-content: center;
    // align-items: center;
    margin-bottom: 35px;
    margin-top: 75px;
    .view-events-header {
        font-size: 45px;
    }
    
    .view-event-description {
        font-size: 25px;
    }

    @media screen and (max-width: 768px) {
        .view-events-header {
            font-size: 25px;
        }
        
        .view-event-description {
            font-size: 20px;
        }
    }
}

.view-event-title {

}



.events-slider-container {
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    // position: absolute;
    // align-items: center;
    width: 100%;
    height: 700px;
    left: 0;
    bottom: 25px;
}

.slick-list {
    height: 610px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

